import { LOAD_MOVIES } from "./Constants";
import FirebaseServices from "../Services/FirebaseServices";


export const loadMovies = () => {
    return async (dispatch) => {
        try {
            await FirebaseServices.getMovies().then((movies) =>
                dispatch({type: LOAD_MOVIES, payload: movies.val()}));
        } catch (err) {
            dispatch({type: LOAD_MOVIES, payload: []});
        }
    }
};