import React from 'react';
import './App.css';
import Navigation from './Pages/Navigation'
import { Search } from "./Pages/Search";
import { Redirect, Route, Switch } from 'react-router';
import List from './Pages/List';

class App extends React.Component {
    render() {
        return (
            <div className="App">
                <Navigation/>
                <Switch>
                    <Route exact path="/movie-list/movieList" component={List}/>
                    <Route exact path="/movie-list/search" component={Search}/>
                    <Redirect to='/movie-list/movieList'/>
                </Switch>
            </div>
        );
    }
}

export default App;
