import * as firebase from "firebase";

class FirebaseServices {
    config = {
        apiKey: "AIzaSyBfzmtoPfA9zIfoyyGxyqRF3zaIoyI2MUM",
        authDomain: "movielist-909a6.firebaseapp.com",
        databaseURL: "https://movielist-909a6.firebaseio.com",
        projectId: "movielist-909a6",
        storageBucket: "movielist-909a6.appspot.com",
        messagingSenderId: "545170456229"
    };
    db;

    constructor() {
        this.initialize();
        this.db = firebase.database();
    }

    initialize() {
        firebase.initializeApp(this.config);
    }

    addMovieToDataBase(movie, seen) {
        movie.seen = seen || false;
        return this.db.ref('/movies/' + movie.title).set(movie);
    }

    getMovies() {
        return this.db.ref('/movies/').once('value');
    }
}

export default new FirebaseServices()