class OmdbServices {
    apiKey = "d52d4b2d";
    imdb = require('imdb-api');

    getMovieByName(movieTitle) {
        return this.imdb.get({name: movieTitle}, {apiKey: this.apiKey, timeout: 30000});
    }

    getMovieById(movieID) {
        return this.imdb.get({id: movieID}, {apiKey: this.apiKey});
    }

    searchMovies(searchTerm) {
        return this.imdb.search({name: searchTerm}, {apiKey: this.apiKey});
    }

}

export default new OmdbServices();